//


const state = {
	// 
	carts: [],
}

// getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 
	carts: state => {
		return state.carts;
	}
}
const mutations = {
	setCarts: (state, carts) => {
		// carts
		const record = state.carts.find(element => element.id == carts.id);
		// 
		if (!record) {
			state.carts.push(carts);
		}
	},
	removeCarts: (state, carts) => {
		state.carts.map((inventor, index) => {
			if (inventor.id == carts.id) {
				state.carts.splice(index, 1);
			}
		});
	},

	clearCarts: (state) => {
		// 
		state.carts = [];
	}
}

// actions->mutations
const actions = {
	// 
	setCarts: ({
		commit
	}, carts) => {
		// mutations
		commit('setCarts', carts);
	},
	removeCarts: ({
		commit
	}, carts) => {
		commit('removeCarts', carts);
	},
	clearCarts: ({
		commit
	}) => {
		commit('clearCarts');
	}

}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


