/**
 *	vuex
 *  gallery
 *  
 */

// json
import newGalleries from '../../data/galleries'

const state = {
	// gallery
	galleries: [],
}

// getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 
	galleries: state => {
		return state.galleries;
	}
}
const mutations = {
	setGallery: (state) => {
		// 
		// copy
		let a = Object.assign([], newGalleries);
		// 
		let copy = JSON.parse(JSON.stringify(a));

		// lodashclonedeep

		// state.gallery
		state.galleries = copy;
	},
	buyGalleries: (state, order) => {
		// -1
		// mapid
		state.galleries.map((inventor) => {
			if (inventor.id == order.id) {
				if (inventor.quantity > 0) {
					inventor.quantity -= 1
				}
			}
		});
	},
	reBackGalleries: (state, order) => {
		state.galleries.map((inventor) => {
			if (inventor.id == order.id) {

				if (inventor.quantity == 0) {
					inventor.quantity += 1
				}

			}
		});
	}
}
const actions = {
	// 
	initGalleries: ({
		commit
	}) => {
		commit('setGallery');
	},
	buyGalleries: ({
		commit
	}, order) => {
		commit('buyGalleries', order);
	},
	reBackGalleries: ({
		commit
	}, order) => {
		commit('reBackGalleries', order);
	},
}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


