const state = {
	// 
	user: null,
	// onAuthStateChanged updatefalse
	isLogin: false,
}

// getter
/**
 * [getter description]
 * @type {Object}
 */
const getters = {
	// 
	isLogin: state => {
		return state.isLogin;
	},
	getUserInfo: state => {
		return state.user;
	}
}
const mutations = {
	login: (state, user) => {

		// isLogintrue
		state.isLogin = true;
		state.user = user;
	},
	logout: (state) => {
		// 
		state.isLogin = false;
		state.user = {};
	}
}

// actions->mutations
const actions = {
	// 
	login: ({
		commit
	}, user) => {
		// mutations
		commit('login', user);

	},
	logout: ({
		commit
	}) => {
		commit('logout');
	}
}

export default {
	state,
	getters,
	mutations,
	actions,
	namespaced: true
}


