<template>
    <!-- <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeIn" mode="in-out"> -->
        <section id="error404">
            <div class="jumbotron content-hight">
                <div class="container text-center">
                    <div class="ab-center">

                        <h2>Ooops!</h2>
                        <br>
                        <!-- vue-count-toplung in -->
                        <!-- renderspan -->
                        <!-- refspanid classclass -->
                        <countTo :startVal='startVal' :endVal='endVal' :duration='2300' ref='error404' class='error404'>
                        </countTo>
                        <br>
                        <button @click="goBack" class="btn btn-default">Go Home</button>
                    </div>
                </div>
            </div>
        </section>
    <!-- </transition> -->
</template>
<script>
// plungin
import countTo from 'vue-count-to';
export default {
    name:'error404',
    // components
    components: {
        countTo
    },
    data() {
        return {
            startVal: 0,
            endVal: 404
        }
    },
    methods: {
        goBack() {
            const vm = this
            // 
            //  
            // vm.$router.go(-1);
            // replacehistory
            vm.$router.replace({
                name: 'home'
            });
        }
    }
};
</script>
<style lang="scss" scoped>
@import '../assets/style/scss/error/error404.scss';
</style>



